import Vue from 'vue';
import App from './App.vue';
import router from './router';
import store from './store';
// store가 vuex임
import './registerServiceWorker';
import vuetify from './plugins/vuetify';

// 파이어베이스 앱 객체 모듈 가져오기
import firebase from 'firebase/compat/app';
//import firebase from 'firebase'
import '@/datasources/firebase';

Vue.config.productionTip = false;

new Vue({
  router,
  store,
  vuetify,

  // vue 생명주기에서 처음 시작(뷰객체 생성)할때 진행되어야하는 이벤트를 모아놓는 곳
  created() {
    // 사용자가 로그아웃, 비밀번호 변경 등의 변경을 했을 때 처리
    firebase.auth().onAuthStateChanged(pUserInfo => {
      if (pUserInfo !== null) {
        // 로그인 했다면
        store.dispatch('fnDoLoginAuto', pUserInfo);
        // 이미 로그인 되어있었는지 등의 상태를 파악하여 스토어에서 처리함(로그인 유지)
      }
    });
  },
  render: h => h(App)
}).$mount('#app');
