<!-- 로그인 후 메인화면을 표시 -->
<template>
    <v-container>
        <v-row>
            <!-- 타이틀 -->
            <v-col col="12" class="mt-5 text-center">
                <h1 class="display-1 my-1">로그인 후 화면 페이지</h1>
                <p class="body-1">로그인을 통해 인증된 사용자가 방문한 페이지입니
                    다.</p>
            </v-col>
        </v-row>
        <v-row>
            <!-- 구글 로그인인 경우, 읽어오기 허용된 정보를 가지고 이미지 표시, 11칸 -->
            <v-col dark offset="3" cols="6" class="mt-5 text-center">
                <img v-if="fnGetUser.photoURL" :src="fnGetUser.photoURL" class="avatar_style" alt="">
                <!-- 계정 이름과 이메일 표시 머스태시로 정보 바인딩,
                    vuex 스토어의 getters에 정의된 fnGetUser()함수로 정보 접근-->
                <h3 class="pt-2 mt-4 grey lighten-2">{{ fnGetUser.name }}</h3>
                <p class="pb-2 grey lighten-2">{{ fnGetUser.email }}</p>
            </v-col>

            <!-- 비밀번호 재설정 표시, 9칸 -->
            <!-- 1안: 구글계정 로그인시엔 비밀번호 재설정 버튼이 안떠야 되지 않나?
                 (구글계정 비밀번호 바꾸는거냐?) -->
            <!-- 2안: 구글계정은 쓰는데(구글계정 비밀번호로 최초 가입및 로그인하고),
                 이 서비스 전용 비번을 설정하여 구분해서 쓰는 경우
                 비밀번호를 변경하고나서 로그아웃, 다시 이메일로 로그인을 시도.
                 구글계정 이메일을 쓰고, 바뀐비밀번호 입력했을때 로그인 되었음 -->
            <v-col offset="3" cols="6" class="text-center mt-1">
                <v-btn @click="fnSendPasswordReset" block color="orange" large dark>
                    <v-icon left>mdi-email</v-icon> 비밀번호 재설정
                </v-btn>
            </v-col>
        </v-row>
    </v-container>
</template>

<script>
// 파이어베이스에서 oFirebaseAuth 객체 변수 가져옴
import { oFirebaseAuth } from '@/datasources/firebase';
export default {
    computed: {
        // 스토어에서 로그인된 계정 정보 반환
        fnGetUser() {
            let oUserInfo = this.$store.getters.fnGetUser;
            return oUserInfo;
        }
    },
    methods: {
        fnSendPasswordReset() {
            // 사용자의 현재 이메일 주소로 비밀번호 재설정 메일 발송하기  
            oFirebaseAuth.sendPasswordResetEmail(this.fnGetUser.email)
            .then(function () {
                alert("비밀번호 재설정메일을 발송했습니다!");
            }).catch(function (error) {
                alert(error);
            });
        }
    }
}
</script>

<style>
/* img태그에 붙음, 원 모양으로 스타일 표시 */
.avatar_style {
    width: 100px;
    height: 100px;
    border-radius: 50%;
}
</style>