// common 모듈 : 시간 지연, 에러메시지 처리의 공통 내용
export default {
  // 현재 상태값
  state: {
    bIsLoading: false, // 처리 중 시간이 걸림을 나타냄
    sErrorMessage: '' // 처리 중 오류 메시지 내용
  },
  // 셋팅값(setters와 같은 개념)
  mutations: {
    // 처리 중 시간이 걸리는지 여부 설정
    fnSetLoading(state, payload) {
      // payload값은 true/false 불리언, 파라미터로 들어온 이 값이 bIsLoading값으로 할당
      state.bIsLoading = payload;
    },
    // 처리 중 오류 메시지 저장
    fnSetErrorMessage(state, payload) {
      // 파라미터로 들어온 payload값이 sErrorMessage값으로 할당
      state.sErrorMessage = payload;
    }
  },
  // state거친 값 반환. 다른 생태주기에서 던져주는거 받아오는 곳
  getters: {
    // 처리 중 시간이 걸리는지 여부 반환
    fnGetLoading(state) {
      return state.bIsLoading;
    },
    // 처리 중 에러 메세지 내용 반환
    fnGetErrorMessage(state) {
      return state.sErrorMessage;
    }
  },
  actions: {
  }
};