import Vue from 'vue';
import VueRouter from 'vue-router';
import start from '../views/start_page.vue';
import main from '../views/main_page.vue';
import login from '../views/login_page.vue';
import register from '../views/register_page.vue';
import err from '../views/error_page.vue';

import firebase from 'firebase/compat/app';

import { oFirebaseAuth } from '../datasources/firebase';


Vue.use(VueRouter);

const routes = [
  {
    path: '/',
    name: 'home',
    component: start
  },
  {
    path: '/start',
    name: 'start',
    component: start
  },
  {
    path: '/main',
    name: 'main',
    component: main,
    meta: { bAuth: true }
  },
  {
    path: '/login',
    name: 'login',
    component: login
  },
  {
    path: '/register',
    name: 'register',
    component: register
  },
  {
    path: '/*',
    // 사용자가 지정된 라우터 페이지 외에 접근시, 무조건에러페이지로 연결
    name: 'err',
    component: err
  },
];

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
});
// 함수에서 각 파라미터의 자리는 하는 일이 정해져 있다.
// 현재 beforeEach()함수에서는 3개의 파라미터를 받는데, 사용하지 않으면 비활성화 된다
// 비활성화는 일반적인 스크립트 소스에서는 문제가 되지 않지만,(유연해서)
// Lint를 추가해서 설치했을 경우 이러한 빈 요소들을 잡아내 에러로 보내버린다
// 이러한 엄격한 스크립트를 작성 할 때에는 사용하지 않는 요소앞에 _를 붙이면 무시처리 할 수 있다.
router.beforeEach((to, from, next) => {
  // 라우터 이동에 개입하여 인증이 필요한 경우 login 페이지로 전환
  const bNeedAuth = to.matched.some(record => record.meta.bAuth);
  // 이동할 페이지의 로그인 여부를 불리언으로 설정, true라면 로그인이 필요한 페이지이다. 
  const bCheckAuth = oFirebaseAuth.currentUser;
  // 파이어베이스에 현재 로그인 사용자 정보 저장

  if (bNeedAuth && !bCheckAuth) {
    // 로그인이 필요한 페이지이고, 현재 로그인 되어있지 않다면 강제로 로그인페이지로 보내라
    next('/login');
  } else {
    // 로그인이 되었으니 원래 가려던데 보내라
    next();
  }
});
export default router;
