<!-- 익명의 사용자가 처음 만나는 화면 -->
<template>
  <v-container>
    <v-row>
      <!-- 타이틀 -->
      <v-col cols="12" class="text-center mt-5">
        <h1 class="display-1 my-1">시작화면 페이지</h1>
        <p class="body-1">로그인 없이 방문자 누구나 접속 가능한 페이지입니다.</p>
        <!-- v-if디렉티브를 이용하여 시간지연상태가 되면 회전 프로그레스 원 표시 -->
        <v-progress-circular v-if="fnGetLoading" indeterminate :width="7" :size="70" color="grey lighten-1">
        </v-progress-circular>
      </v-col>
      <!-- 구글 계정 로그인 버튼 표시 및 처리, 9칸 -->
      <v-col offset="3" cols="6" class="text-center mt-5">
        <!-- 버튼 클릭시 Vuex store 모듈 provier의 구글계정회원로그인 팝업 기능 실행 -->
        <v-btn @click="fnDoGoogleLogin_Popup" block outlined color="red" large dark>
          <v-icon left color="red">mdi-google</v-icon>
          구글 로그인
        </v-btn>
      </v-col>
      <!-- 이메일 계정 로그인 버튼 표시 및 처리, 9칸 -->
      <v-col offset="3" cols="6" class="text-center mt-5">
        <!-- 링크처럼 사용, 클릭시 로그인 페이지로 이동 -->
        <v-btn to="/login" block color="red" large dark>
          <v-icon left>mdi-email</v-icon>
          이메일 로그인
        </v-btn>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
export default {
  // 이벤트
  methods: {
    fnDoGoogleLogin_Popup() { // 스토어에 구글계정 로그인 처리 요청
      this.$store.dispatch("fnDoGoogleLogin_Popup");
    }
  },
  // 복잡구성요소 미리 만들어놓기
  computed: {
    // 시간지연상태 스토어에서 읽어서 반환
    fnGetLoading() {
      return this.$store.getters.fnGetLoading;
    }
  },
};
</script>
