// 파이어베이스 앱 객체 모듈 가져오기
// import firebase from 'firebase/compat/app';
// 파이어베이스 패키지 모듈 가져오기
// import 'firebase/compat/auth';

// provider 모듈 : 구글과 이메일 인증 처리 내용

// 파이어베이스에서 oFirebaseAuth 객체 변수 가져옴
import { oFirebaseAuth } from '@/datasources/firebase';
// Google 제공업체 객체의 인스턴스를 생성을 위해 객체 변수 가져옴
import {
  signInWithPopup,
  GoogleAuthProvider,
  createUserWithEmailAndPassword,
  signInWithEmailAndPassword,
} from "firebase/auth";
import router from '@/router';


// provider는 이메일과 구글인증을 받을 수 있는 기능이 있다.
// 인증관리의 가장 핵심 부분!!
export default {
  state: {
    oUser: null // 사용자 정보를 담을 객체
  },
  mutations: {
    // actions에 있는 파이어베이스 외부 API에서 반환되는 사용자 정보를 파라미터로 받아 oUser객체에 저장
    fnSetUser(state, payload) {
      state.oUser = payload;
    }
  },
  getters: {
    // 사용자 객체를 반환
    fnGetUser(state) {
      return state.oUser;
    },
    // 사용자 객체의 값의 유무로 로그인 여부 반환
    // 로그인 했다 true
    fnGetAuthStatus(state) {
      return (state.oUser != null);
    }
  },
  // 파이어베이스 외부 API이용(긁어다 붙일것), 전부 프로미스 처리, 프로미스객체가 commit()값을 넘긴다.
  // oFirebaseAuth를 첫번째 파리미터로 받고 외부API 5종을 전부 단일 콜링으로 바꾼다.
  actions: {
    // 이메일 회원 가입 처리 createUserWithEmailAndPassword(Auth, Email, Password)
    fnRegisterUser({ commit }, payload) {
      commit('fnSetLoading', true); // 스토어에 시간걸림으로 상태 변경
      // 파이어베이스에 이메일 회원 생성 및 저장
      createUserWithEmailAndPassword(oFirebaseAuth, payload.pEmail, payload.pPassword)
        .then(pUserInfo => {
          // 신규 회원 이메일 정보를 스토어에 저장
          commit('fnSetUser', {
            email: pUserInfo.email  // <-- 파이어베이스 v9 마이그레이션 : user 추가
          });
          commit('fnSetLoading', false); // 스토어에 시간완료 상태 변경
          commit('fnSetErrorMessage', ''); // 스토어 에러메시지 초기화
          router.push('/main'); // 로그인 후 첫 화면으로 이동
        })
        .catch(err => {
          commit('fnSetErrorMessage', err.message);
          commit('fnSetLoading', false);
        });
    },
    // 이메일 회원 로그인 signInWithEmailAndPassword(Auth, Email, Password)
    fnDoLogin({ commit }, payload) {
      commit('fnSetLoading', true); // 스토어에 시간걸림으로 상태 변경
      // 파이어베이스에 이메일 회원 로그인 인증 처리 요청
      signInWithEmailAndPassword(oFirebaseAuth, payload.pEmail, payload.pPassword)
        .then(pUserInfo => {
          // 로그인이 성공하면 스토어에 계정정보 저장
          commit('fnSetUser', {
            id: pUserInfo.uid,
            name: pUserInfo.displayName,
            email: pUserInfo.email,
            photoURL: pUserInfo.photoURL
          });
          commit('fnSetLoading', false); // 시간걸림 상태 해제
          commit('fnSetErrorMessage', ''); // 에러메세지 초기화
          router.push('/main'); // 로그인 후 화면으로 이동
        })
        .catch(err => {
          commit('fnSetErrorMessage', err.message);
          commit('fnSetLoading', false);
        });
    },
    // var 변수 = new GoogleAuthProvider()
    // 구글 계정 회원 로그인(팝업) signInWithPopup(Auth, 옵션)
    fnDoGoogleLogin_Popup({ commit }) {
      commit('fnSetLoading', true); // 스토어에 시간걸림으로 상태 변경
      // 파이어베이스에 구글 회원 로그인 인증 처리 요청
      // 로그인제공자객체를 생성
      var oProvider = new GoogleAuthProvider(); // 로그인 제공자 객체 생성
      // 오픈 계정의 범위를 설정합니다. 빌드 설정 시 옵션 선택 여부와 맞춰준다.
      // https://developers.google.com/identity/protocols/googlescopes
      oProvider.addScope('profile');
      oProvider.addScope('email');

      signInWithPopup(oFirebaseAuth, oProvider)
        .then(pUserInfo => {
          // 로그인이 성공하면 스토어에 계정정보 저장
          commit('fnSetUser', {
            id: pUserInfo.user.uid,
            name: pUserInfo.user.displayName,
            email: pUserInfo.user.email,
            photoURL: pUserInfo.user.photoURL
          });
          commit('fnSetLoading', false); // 시간걸림 상태 해제
          commit('fnSetErrorMessage', ''); // 에러메세지 초기화
          router.push('/main'); // 로그인 후 화면으로 이동
        })
        .catch(err => {
          commit('fnSetErrorMessage', err.message);
          commit('fnSetLoading', false);
        });
      /**
       * 현재 도메인에 OAuth 작업 권한이 없으면 signInWithPopup,
       * signInWithRedirect, linkWithPopup 및 linkWithRedirect가 작동하지 않음.
       * 도메인(192.168.0.51)을 Firebase 콘솔 -> 인증 -> 설정 -> 인증된 도메인
       * 탭의 OAuth 리디렉션 도메인 목록 사용할 도메인 목록을 전부 추가한다
       */
    },
    // 자동 로그인 처리,  main.js의 vue 객체에서 create()메서드가 로그인 상태를 체크하는 부분인 store.dispatch()메서드에 사용되는 파라미터함수
    fnDoLoginAuto({ commit }, pUserInfo) {
      // 자동 로그인 시 스토어에 계정정보 저장
      commit('fnSetUser', {
        id: pUserInfo.user.uid,
        name: pUserInfo.user.displayName,
        email: pUserInfo.user.email,
        photoURL: pUserInfo.user.photoURL
      });
      commit('fnSetLoading', false); // 시간걸림 상태 해제
      commit('fnSetErrorMessage', ''); // 에러메세지 초기화  
    },

    // 로그아웃 처리 .signOut()
    fnDoLogout({ commit }) {
      // 파이어베이스에 로그아웃 요청
      oFirebaseAuth.signOut();
      commit('fnSetUser', null); // 스토어에 계정정보 초기화
      router.push('/'); // 첫 화면으로 이동
    }
  }
};