<!-- 이메일 로그인페이지 -->
<template>
  <v-container flow>
    <v-row>
      <!-- 타이틀 -->
      <v-col cols="12" class="text-center my-5">
        <h1 class="display-1">로그인 페이지</h1>
      </v-col>
    </v-row>
    <v-row>
      <!-- 반응형에 따라 다르게 너비 조절 -->
      <v-col class="text-center" cols="8" offset="2" sm="6" offset-sm="3">
        <!-- submit의 기본 기능이 양식의 입력이 제출 되면 페이지를 다시 로드한다.
            .prevent 사용하면 @클릭 submit 이벤트 후에 리로드 막을 수 있다 -->
        <form @submit.prevent="fnDoLogin">
          <v-text-field name="Email" label="이메일" type="email" v-model="sEmail" required> </v-text-field>
          <v-text-field name="Password" label="비밀번호" type="password" v-model="sPassword" required> </v-text-field>

          <!-- v-if 디렉티브를 이용하여 시간지연상태인 경우 로그인 버튼은 사라지게 함 -->
          <v-btn type="submit" v-if="!fnGetLoading" color="orange" dark> 로그인 </v-btn>
          <v-progress-circular v-if="fnGetLoading" indeterminate :width="7" :size="70" color="grey lighten-1"> </v-progress-circular>
          
          <!-- 오류 메시지가 있을 경우 표시 -->
          <v-alert class="mt-3" type="error" dismissible v-model="bAlert"> {{ fnGetErrMsg }} </v-alert>
        </form>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
export default {
  data() {
    return {
      bAlert: false, // 오류 메시지 표시 여부
      sEmail: '', // 이메일 입력값 임시 저장
      sPassword: '' // 비밀번호 입력값 임시 저장
    };
  },
  computed: {
    // 오류메시지 스토어에서 읽어서 반환
    fnGetErrMsg() {
      return this.$store.getters.fnGetErrorMessage;
    },
    // 시간지연상태 스토어에서 읽어서 반환
    fnGetLoading() {
      return this.$store.getters.fnGetLoading;
    }
  },
  methods: {
    // 스토어에 이메일 로그인 처리 요청
    fnDoLogin() {
      this.$store.dispatch('fnDoLogin', {
        pEmail: this.sEmail,
        pPassword: this.sPassword
      });
    }
  },
  // watch속성을 이용하여 함수, 변수 모두 모니터링 하여 오류메시지 출력가능
  watch: {
    // computed속성에 의해 fnGetErrMsg 함수가 오류메시지를 반환하면 오류 메시지 표시
    fnGetErrMsg(pMsg) {
      if (pMsg) this.bAlert = true;
    },
    // 변수값도 모니터링
    bAlert(pValue) {
      // bAlert 값이 false로 바뀌면 오류메시지 초기화
      if (pValue == false) this.$store.commit('fnSetErrorMessage', '');
    }
  }
}
</script>